.searchBlock {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: flex-start;
  flex: 1 1 300px;
  background-color: #f3f3fa;
  border-radius: 6px;
  padding: 8px 14px 8px 14px;
  border: 1px solid #999999;
  margin-right: 10px;
}

.searchBlock img {
  position: absolute;
  height: 14px;
  width: 14px;
  margin: 0;
}

.searchBlock input {
  margin-left: 20px;
  /*width: 750px;*/
  border: none;
  outline: none;
}

.search {
  margin-left: 11px;
  border: none;
}

