.pRecContainer {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 100vh;
}

.pRecWrapper {
  display: flex;
  flex-direction: column;
  align-items: center;
  width: 413px;
  background: #f9f9fe;
  border-radius: 8px;
  padding: 0 33px 31px;
  min-height: 460px;
}

.pRecWrapper h2,
h3 {
  font-family: 'Poppins', sans-serif;
  font-weight: 600;
}

.pRecWrapper h2 {
  margin-top: 32px;
  font-size: 26px;
}

.pRecWrapper h3 {
  font-size: 22px;
  margin-bottom: 39px;
}

.pRecWrapper p {
  font-family: 'Roboto', sans-serif;
  font-weight: 400;
  font-size: 16px;
  opacity: 0.5;
  margin-top: 30px;
  text-align: center;
}

.pRecWrapper button {
  margin-top: 90px;
}

.pRecWrapper .link {
  font-family: 'Roboto', sans-serif;
  font-weight: 600;
  font-size: 16px;
  color: #21268f;
  text-decoration: none;
  margin-top: 11px;
}