.paginationContainer {
  display: flex;
  /*align-self: flex-end;*/
  margin-top: 20px;
}

.pagesContainer {
  display: flex;
  flex-direction: row;
}

.paginationItem {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-weight: 400;
  font-size: 14px;
  color: #808080;
  padding: 5px;
  height: 25px;
  margin: 0 4px 0 4px;
  border-radius: 3px;
  cursor: pointer;
}

.listPages {
  flex-direction: row;
  width: auto;
  cursor: auto;
}

.selectedPage {
  background: #0064eb;
  color: white;
}

.arrow {
  width: 7.12px;
  height: 11.41px;
}

.dots {
  cursor: auto;
}

.disabled {
  opacity: 0.5;
  cursor: not-allowed;
  transition: none;
  transform: none;
}

@media screen and (max-width: 450px) {
  .paginationContainer {
    flex-direction: column;
    align-self: center;
  }
}
